<template>
  <div class="details">
    <div class="btn-list">
      <mt-button class="btn" @click.native="tovideo">1</mt-button>
      <mt-button class="btn" @click.native="tovideo">2</mt-button>
      <mt-button class="btn" @click.native="tovideo">3</mt-button>
      <mt-button class="btn" @click.native="tovideo">4</mt-button>
      <mt-button class="btn" @click.native="tovideo">5</mt-button>
      <mt-button class="btn" @click.native="tovideo">6</mt-button>
      <mt-button class="btn" @click.native="tovideo">7</mt-button>
      <mt-button class="btn" @click.native="tovideo">8</mt-button>
      <mt-button class="btn" @click.native="tovideo">9</mt-button>
      <mt-button class="btn" @click.native="tovideo">10</mt-button>
      <mt-button class="btn" @click.native="tovideo">11</mt-button>
      <mt-button class="btn" @click.native="tovideo">12</mt-button>
    </div>
  </div>
</template>
<script>

export default {
  name: 'Details',
  data () {
    return {
      // msg: 'Welcome to Your Vue.js App'
      msg: '欢迎学习VUE1!'
    }
  },
  components: { },
  methods: {
    tovideo () {
      this.$router.togo('/Details/VideoPlayer')
    }
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.details {
  width: 100%;
  height: 100vh;
  background-image: url(../assets/121.jpg);
  background-size: cover;
  /*position: fixed;*/
  display: flex;
  flex-flow: column;
  justify-content: center;
}

.btn {
  width: 40vw;
  height: 20vw;
  margin: 2vw;
}

</style>



